import { ApiHref } from '@/core/enums/api.enum';
import { IUser } from '@/core/interfaces/user.interface';
import { get, remove } from '@/core/utills/api.utills';

export default {
  state: {
    users: [],
  },

  mutations: {
    setUsers(state: any, users: IUser[]) {
      state.users = users;
    },

    deleteUser(state: any, id: number) {
      const users: IUser[] = state.users;
      state.users = users.filter((user) => user.id !== id);
    },
  },

  getters: {
    users: (state: any): IUser[] => state.users,
  },

  actions: {
    async loadUsers({ commit }: any) {
      const res = await get(ApiHref.GET_USERS);
      const users: IUser[] = res.data.users;
      commit('setUsers', users);
    },

    async deleteUser({ commit }: any, { id }: any) {
      const res = await remove(ApiHref.DELETE_USER, { id });
      if (res.status === 200) {
        commit('deleteUser', id);
      }
    },
  },
};
